
import {computed, onMounted, ref} from "vue";
import ApiService from "@/core/services/ApiService";
import {useStore} from "vuex";

export default {
  name: "TransshipmentTE",
  props: {
    tableData: {
      type: Array,
    },
  },

  setup() {
    const store = useStore();
    const loading = ref(false);
    const bookingTableData = computed(() => store.getters.Bookings);
    const elements = ref({
      loading: false,
      options: [],
      list: [],
    });
    const headers = ref([
      { label: "tibookings", prop: "booking_id" },
      { label: "timaritimetripslist", prop: "maritime_voyage_id" },
      { label: "iremarks", prop: "remarks" },
    ]);
    const headerTable = ref(headers.value);

    const getElements = () => {
      ApiService.get("/api/maritimevoyages").then(({ data }) => {
        elements.value.list = data;
        elements.value.options = data;
      });
    };
    const selectElements = (query) => {
      if (query !== "") {
        elements.value.loading = true;
        setTimeout(() => {
          elements.value.loading = false;
          elements.value.options = elements.value.list.filter(
            (item: Record<string, any>) => {
              return item.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
            }
          );
        }, 200);
      } else {
        elements.value.options = [];
      }
    };

    onMounted(() => getElements());

    return {
      loading,
      bookingTableData,
      elements,
      headers,
      headerTable,
      selectElements,
    };
  },
};
